<template>
    <div class="support-list">
        <!--<van-cell title="贡献榜" is-link/>-->
        <ul v-if="list.list.length>0" class="contribution-list">
            <li class="No" :key="index" v-for="(item,index) in list.list">
                <div class="contribution-No">{{index+1}}</div>
                <div class="contribution-info">
                    <img class="contribution-head" :src="item.userHeadUrl" alt="">
                    <p class="contribution-name ">{{item.userName}}</p>
                    <div v-if="type==='积分'" class="contribution-value">{{item.contribution}}{{type}}</div>
                    <div v-if="type==='元'" class="contribution-value">¥{{(item.contribution/100).toFixed(2)}}</div>
                </div>
            </li>
        </ul>
        <div class="default-page" v-if="list.length===0">暂无数据</div>
    </div>
</template>

<script>
  export default {
    name: "SupportList",
    data() {
      return {
        type: '',
        list: []
      }
    },
    created() {
      document.title = '应援榜'
    },
    mounted() {
      let params = this.$queryParams(location.href);
      this.getSupportList(params);
      this.type = params.type;

    },
    methods: {
      getSupportList: function (params) {
        this.$axios({
          method: 'post',
          url: process.env.VUE_APP_BASE4 + '/assistance/singerActivityContributionListPage',//贡献榜
          data: this.$qs.stringify({
            id: params.id,
            pageNum: 1,
            pageSize: 20
          })
        }).then((res) => {
            if (res.data.status !== 1000) {
              return false;
            } else if (res.data.data !== null) {
              this.list = res.data.data;
              for (let i = 0; i < this.list.length; i++) {
                if (this.list[i].type === 0) {
                  this.list[i].contribution = (this.list[i].contribution / 100);
                }
              }
            }
          }
        ).catch((err) => {
          console.log(err);
        })
      }
    }
  }
</script>

<style scoped lang="scss">
    .support-list {
        height: 100%;
        width: 100%;
        background: #fff;
    }

    .default-page {
        height: 100vh;
        width: 100%;
        text-align: center;
        line-height: 8rem;

    }

    .contribution-list {
        width: 100%;
        background: #fff;
        .No {
            height: 1.18rem;
            padding: 0 .3rem;
            .contribution-No {
                display: block;
                height: .46rem;
                width: .38rem;
                margin: .36rem 0;
                float: left;
                text-align: center;
                color: #909090;
                line-height: .44rem;
                font-size: .34rem;
            }
            &:nth-of-type(1) {
                .contribution-No {
                    background: url("../../../assets/images/yi.png") center no-repeat;
                    background-size: 100% 100%;
                    color: #fff;
                    font-size: .2rem;

                }
            }
            &:nth-of-type(2) {
                .contribution-No {
                    background: url("../../../assets/images/er.png") center no-repeat;
                    background-size: 100% 100%;
                    color: #fff;
                    font-size: .2rem;

                }
            }
            &:nth-of-type(3) {
                .contribution-No {
                    background: url("../../../assets/images/san.png") center no-repeat;
                    background-size: 100% 100%;
                    color: #fff;
                    font-size: .2rem;

                }
            }

            .contribution-info {
                height: 100%;
                width: 91%;
                float: right;
                line-height: 1.18rem;
                border-bottom: 1px solid #f2f2f2;
                display: flex;
                justify-content: space-between;
                .contribution-head {
                    display: block;
                    height: .73rem;
                    width: .73rem;
                    margin: .225rem .225rem .225rem 0;
                    -webkit-border-radius: 50%;
                    -moz-border-radius: 50%;
                    border-radius: 50%;
                }
                .contribution-name {
                    flex: 1;
                    color: #333333;
                    font-size: .28rem;
                    font-weight: 600;
                    overflow: hidden;
                    -ms-text-overflow: ellipsis;
                    text-overflow: ellipsis;
                    white-space: nowrap;
                }
                .contribution-value {
                    color: #FF2E4D;
                    font-size: .34rem;
                    font-weight: 600;
                    overflow: hidden;
                    -ms-text-overflow: ellipsis;
                    text-overflow: ellipsis;
                    white-space: nowrap;
                }

            }
        }

    }
</style>
